<template>
  <v-container>
    <v-sheet color="rgba(0,0,0,0.7)" rounded>
      <v-row class="white--text">
        <v-col cols="12" class="text-center">
          <span class="white--text text-md-h3 text-h4">Justificar Falta</span>
        </v-col>
      </v-row>
      <v-row v-if="No_FJ < 3">
        <v-col cols="12" md="3" class="text-center">
          <span class="white--text text-md-h5 text-h6">Justificar por: </span>
        </v-col>
        <v-col cols="12" md="3" class="white--text text-center">
          <v-radio-group v-model="radios" mandatory dark>
            <v-radio label="Falta De Horas" value="1"></v-radio>
            <v-radio label="Omiti Salida" value="2"></v-radio>
          </v-radio-group>
        </v-col>

        <v-col
          cols="12"
          md="3"
          class="white--text text-center"
          v-if="radios == 1 && false"          
        >
          <span class="white--text text-md-h5 text-h6">Falta por: </span>
        </v-col>
        <v-col cols="12" md="3" class="text-center" v-if="false">
          <v-radio-group v-model="radios2" mandatory dark v-if="radios == 1">            
            <v-radio label="Incidencia" value="2"></v-radio>
          </v-radio-group>
        </v-col>
      </v-row>
      <v-row v-if="radios2 == 2 && radios == 1 && dias.length && No_FJ < 3">
        <v-col cols="12" md="3" class="text-center">
          <span class="white--text text-md-h5 text-h6"
            >Seleccione Una Fecha:</span
          >
        </v-col>
        <v-col cols="12" md="5" class="text-center text-capitalize">
          <v-date-picker
            dark
            :allowed-dates="allowedDates2"
            v-model="fecha"
            :max="max"
            :min="min"
            class="text-capitalize"
          ></v-date-picker>
        </v-col>
        <v-col cols="12" md="4">
          <v-select
            v-model="valor"
            :items="Nombres_Incidencias"
            label="Motivo"
            dark
          ></v-select>
        </v-col>
        <v-col cols="12" md="5" v-if="valor && fecha">
          <v-btn small @click="Justificar_Falta()" block dark>Confirmar</v-btn>
        </v-col>
      </v-row>
      <v-row v-if="radios == 2 && dias2.length && No_FJ < 3">
        <v-col cols="12" md="3" class="text-center">
          <span class="white--text text-md-h5 text-h6"
            >Seleccione Una Fecha:</span
          >
        </v-col>
        <v-col cols="12" md="5" class="text-center text-capitalize">
          <v-date-picker
            dark
            :allowed-dates="allowedDates3"
            v-model="fecha"
            :max="max"
            :min="min"
            class="text-capitalize"
          ></v-date-picker>
        </v-col>

        <v-col cols="12" md="5" v-if="fecha">
          <v-btn small @click="Justificar_Omision()" block dark>Confirmar</v-btn>
        </v-col>
      </v-row>
      <v-row v-if="dias2.length==0 && No_FJ < 3">
        <v-col cols="12" md="3" class="text-center">
          <span class="white--text text-md-h5 text-h6"
            >No Hay Datos Para Mostrar</span
          >
        </v-col>       
      </v-row>
      <v-row v-if=" No_FJ >= 3">
        <v-col cols="12" md="3" class="text-center">
          <span class="white--text text-md-h5 text-h6"
            >Ya no puedes justificar más</span
          >
        </v-col>       
      </v-row>
    </v-sheet>
  </v-container>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import axios from "axios";
export default {
  computed: {
    ...mapState(["ip"]),
  },
  mounted() {    
    
    this.allowedDates();
    this.Cargar_Motivos();
    this.No_FALTAS_Justificadas()
  },
  data() {
    return {
      radios: null,
      radios2: null,
      //Calendario
      fecha: "",
      min: null,
      max: null,
      dias: [],
      //Dias con falta
      Faltas_Dias: [],
      picker: null,
      // Omision Salida
      Faltas_Omision: [],
      dias2: [],
      //Para lista de incidencia
      Incidencias: [],
      Nombres_Incidencias: [],
      valor: "",
      No_FJ:0
    };
  },
  methods: {
    ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",      
    ]),
    allowedDates() {
      let hoy = new Date();
      let inicio = new Date(hoy);
      this.max = hoy.toISOString().substring(0, 10);
      if (hoy.getDate() <= 15) {
        inicio.setDate(1);
      } else {
        inicio.setDate(16);
      }
      this.min = inicio.toISOString().substring(0, 10);
      
      this.Cargar_Datos();
    },
    allowedDates2(val) {
      return this.dias.includes(val);
    },
    allowedDates3(val) {
      return this.dias2.includes(val);
    },
    async No_FALTAS_Justificadas() {
      this.Docentes=[]
      this.id_Docentes=[]
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Faltas");
      this.Modificar_Espera(true);

      try {
        const json = JSON.stringify({
          token: localStorage.getItem("Token"),
          fechaI: this.min,
          fechaF: this.max,
        });
        //      console.log(json);
        // let res = await axios.post(
        //   "http://" + this.ip + "/api/jefe_departamento/Falta_Justificada",
        //   json,
        //   {
        //     headers: {
        // Overwrite Axios's automatically set Content-Type
        //       "Content-Type": "application/json",
        //     },
        //   }
        // );
        
        let res = await axios.post(
          "http://" + this.ip + "/api/docente/No_FJ",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        
        if (!this.error && !res.data.Error) {
          
          this.No_FJ=res.data.Datos;          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
         this.Modificar_cargar(false); 
          
        } else if (res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data.status);
          this.Modificar_Boton(true);
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);

        console.log(error);
      }

    },

    async Cargar_Datos() {
      this.fecha = "";
      this.valor = "";
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Registro");
      this.Modificar_Espera(true);

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        id: this.no_trabajador,
        fechaI: this.min,
        fechaF: this.max,
      });

      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/docente/Falta-Rango",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Cargando Faltas");          
          const registros_F = [];
          const dia_f = [];
          const registros_O = [];
          const dia_o = [];
          res.data.Datos.forEach((element) => {
            if (element.No_MotivoF == 1) {
              registros_O.push({
                id: element.No_Falta,
                Nombre: element.Nombre,
                ApellidoP: element.ApellidoP,
                Fecha: element.Fecha.substring(0, 10),
                No_Empleado: element.No_Empleado,
              });
              dia_o.push(element.Fecha.substring(0, 10));
            } else {
              registros_F.push({
                id: element.No_Falta,
                Nombre: element.Nombre,
                ApellidoP: element.ApellidoP,
                Fecha: element.Fecha.substring(0, 10),
                No_Empleado: element.No_Empleado,
              });
              dia_f.push(element.Fecha.substring(0, 10));
            }
          });
          this.dias = dia_f;
          this.Faltas_Dias = registros_F;

          this.Faltas_Omision = registros_O;
          this.dias2 = dia_o;
          
          this.Modificar_Boton(true);
        } else if (res.data.Error) {          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos("Sin Registros");
          this.Modificar_Boton(true);
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        console.log(error);
      }
    },
    async Cargar_Motivos() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Motivos");
      this.Modificar_Espera(true);
      this.Nombres_Incidencias = [];
      this.Incidencias = [];
      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
      });

      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/motivo/all",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Cargando Datos");
          
          res.data.Datos.forEach((element) => {
            this.Nombres_Incidencias.push(element.Descripcion);
            this.Incidencias.push(element);
          });

          this.Modificar_Boton(true);

          
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos("Sin Registros");
          this.Modificar_Boton(true);
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        console.log(error);
      }
    },
    async Justificar_Omision() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Justificando");
      this.Modificar_Espera(true);
      

      let falta = {};
      this.Faltas_Omision.forEach((element) => {
        
        if (element.Fecha == this.fecha) {
          falta = { No_Falta: element.id };
        }
      });

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        No_Falta: falta.No_Falta,
        FechaS: new Date().toISOString().substring(0, 10),
        No_Motivo: -1,
      });      
      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/justificante/",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Justificado");
          this.Modificar_Boton(true);
          this.Cargar_Datos();
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data);
          this.Modificar_Boton(true);
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        console.log(error);
      }
    },
    async Justificar_Falta() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Justificando");
      this.Modificar_Espera(true);
      

      let falta = {};
      this.Faltas_Dias.forEach((element) => {
        
        if (element.Fecha == this.fecha) {
          falta = { No_Falta: element.id };
        }
      });

      const justificacion = this.Incidencias.find(
        (element) => element.Descripcion == this.valor
      );

      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        No_Falta: falta.No_Falta,
        FechaS: new Date().toISOString().substring(0, 10),
        No_Motivo: justificacion.No_Motivo,
      });

      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/justificante/",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );

        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Justificado");
          this.Modificar_Boton(true);
          this.Cargar_Datos();
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data);
          this.Modificar_Boton(true);
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        console.log(error);
      }
    },
  },
};
</script>